<template>
  <div class=" text-center loginscreen  animated fadeInDown modify">
    <div class="row mt">
      <h1>系统可见模块设置</h1>
      <form class="m-t form-horizontal middle-box">
        <div class="form-group">
          <label class="control-label col-sm-3">基本信息</label>
          <div class="col-sm-9">
            <select class="form-control" v-model="form.basicIn_formation">
              <option value="1">可见</option>
              <option value="0">不可见</option>
            </select>
          </div>
        </div>
        <div class="form-group">
          <label class="control-label col-sm-3">学术成果</label>
          <div class="col-sm-9">
            <select class="form-control" v-model="form.academic_achievements">
              <option value="1">可见</option>
              <option value="0">不可见</option>
            </select>
          </div>
        </div>
        <div class="form-group">
          <label class="control-label col-sm-3">人才培养</label>
          <div class="col-sm-9">
            <select class="form-control" v-model="form.talent_development">
              <option value="1">可见</option>
              <option value="0">不可见</option>
            </select>
          </div>
        </div>
        <div class="form-group">
          <label class="control-label col-sm-3">评分系统</label>
          <div class="col-sm-9">
            <select class="form-control" v-model="form.score">
              <option value="1">可见</option>
              <option value="0">不可见</option>
            </select>
          </div>
        </div>
        <div class="form-group">
          <label class="control-label col-sm-3">管理工作</label>
          <div class="col-sm-9">
            <select class="form-control" v-model="form.management_work">
              <option value="1">可见</option>
              <option value="0">不可见</option>
            </select>
          </div>
        </div>

        <div class="form-group">
          <div class="col-sm-offset-3 col-sm-9">
            <b class="btn btn-primary block full-width" @click="confirm()">确定</b>
          </div>
        </div>
      </form>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
import Service from '@/assets/service/service'
export default {
  name: 'setting',
  data () {
    return {
      form: {}
    }
  },
  methods: {
    confirm () {
      if (!this.form.id) {
        toastr.error('系统参数错误')
        return
      }
      Service.PageSet.setting(this.form.id, this.form).then(response => {
        if (response.code == 200) {
          toastr.success('修改成功,自动刷新页面')
          setTimeout(() => window.parent.location.reload(), 1000)
        }
      })
    }
  },
  created () {
    Service.PageSet.get().then(response => this.form = response.data)
  }
}
</script>

<style scoped>
  .mt{
    margin: 100px 0 0;
  }
  .mt h1{
    font-weight: bold;
  }
  .modify {
    position: relative;
  }
</style>



